import Vue from 'vue'
import VueRouter from 'vue-router'
import Covid from '../pages/Covid.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Covid',
    component: Covid
  },
  {
    path: '/economy',
    name: 'Economy',
    component: () => import('../pages/Economy.vue')
  },
  {
    path: '/migrate',
    name: 'Migrate',
    component: () => import('../pages/Migrate.vue')
  },
  {
    path: '/district',
    name: 'District',
    component: () => import('../pages/District.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
